let stp = require("../../iuc/roomApplication/stepsv1.js")
export default {
	name: "LabStepInfo",
	props: {
		value: {
			required: true,
			type: Array,
			default: () => {}
		}
	},
	data() {
		return {
			colorDic: {
				0: '',
				10: 'text-cyan',
				20: 'text-blue',
				30: 'text-red'
			},
			iconDic:{
				0:'',
				10: 'loading',
				20: '',
				30: 'err'
			},
			steps: []
		};
	},
	watch: {
		value: {
			deep: true,
			immediate: true,
			handler(value) {
				this.steps = this.value.map(value => {
					value.icon=this.iconDic[value.status];
					value.color=this.colorDic[value.status];
					return value;
				})
			}
		}
	},
}
